.form-feedback__emotions-wrapper {
  margin-top: 30px;
}

.form-feedback__textarea {
  width: 100%;
  height: 200px;
}

@media (min-width: @tablet-width) {
  .form-feedback__textarea {
    width: 496px;
    height: 189px;
    margin: 0 auto;
  }

  .form-feedback__fieldset--emotions {
    width: 100%;
    border-style: groove;
    border-color: #ececec;
    border-image: initial;
    padding: 0;
    padding-top: 30px;
    padding-bottom: 30px;
    border-width: 2px;
  }

  .form-feedback__emotions-wrapper {
    width: 100%;
    order: 4;
  }
}

@media (min-width: @desktop-width) {
  .form-submit {
    display: flex;
    align-items: center;
    justify-content: space-between;
    order: 5;
  }

  .form-submit__notification {
    margin-top: 40px;
  }

  .form-feedback__textarea {
    width: 816px;
    height: 124px;
    padding-top: 45px;
    padding-bottom: 45px;
  }

  .form-feedback__fieldset--emotions {
    padding: 0;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
