.contact {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

#map {
  width: 100%;
  height: 200px;
}

.contact__text {
  width: 222px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 44px;
  font-size: 24px;
}

.contact__bottom-wrapper {
  width: 100%;
  position: relative;
}

.contact__map-marker {
  width: 50px;
  position: relative;
  height: 50px;
  background-image: url("../img/map-marker-svg.svg");
  background-repeat: no-repeat;
  top: 130px;
  left: 150px;
}

.contact__map-inner {
  width: 100%;
  margin: 0 auto;
}

.contact__map-inner::before {
  content: "";
  margin-left: -170px;
  position: absolute;
  width: 0;
  height: 0;
  border-left: 179px solid transparent;
  border-right: 179px solid transparent;
  border-top: 57px solid white;
  z-index: 99;
}

.contact__item a {
  color: #d22856;
  list-style: none;
  text-decoration: none;
  height: 22px;
  font-size: 24px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
}

.contact__list {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  list-style: none;
  padding-left: 40px;
  margin: 0 auto;
}

.contact__item b {
  color: black;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  list-style: none;
}

.contact__item {
  list-style: none;
  margin-bottom: 31px;
}

.contact__item:first-child {
  margin-top: -13px;
}

.contact-map {
  width: 100%;
  height: 200px;
  background-size: 100%, 100%;
  margin: 0 auto;

}

.contact__academy-link:hover {
   color: #283645;
 }

.contact__academy-link:active {
  color: rgba(40, 54, 69, 0.3);
}

.contact__map-inner::before {
  top: -1px;
  margin-left: -50vw;
  position: absolute;
  width: 0;
  height: 0;
  border-left: 50vw solid transparent;
  border-right: 50vw solid transparent;
  border-top: 57px solid white;
  z-index: 99;
}

@media (min-width: @tablet-width) {
  .contact {
    margin: 0 auto;
    text-align: center;
    list-style: none;
    margin-bottom: 30px;
  }

  #map {
    height: 400px;
  }

  .contact-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .contact__list {
    width: 550px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    padding-left: 0;
  }

  .contact__text {
    width: 400px;
    margin: 0 auto;
    margin-bottom: 69px;
    font-size: 40px;
  }

  .contact__item {
    width: 50%;
    margin-bottom: 73px;
    margin-left: 0;
    line-height: 41px;
    margin-top: -21px;
  }

  .contact-map {
    height: 300px;
    margin: 0 auto;
    margin-top: -42px;
  }

  .contact__list {
    justify-content: center;
  }
}

@media (min-width: @desktop-width) {
  .contact {
    margin: 0 auto;
    text-align: center;
  }

  #map {
    height: 400px;
  }

  .contact__list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    padding-left: 0;
  }

  .contact__text {
    width: 500px;
    margin: 0 auto;
    margin-bottom: 70px;
    font-size: 50px;
  }

  .contact-map {
    width: 100%;
    margin: 0 auto;
  }

  .contact-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    flex-wrap: nowrap;
  }

  .contact__item {
    width: auto;
  }

  .contact__item--two {
    order: 1;
  }

  .contact__item--one {
    order: 2;
  }
}
