.form-contact__list {
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding: 0;
  padding-top: 40px;
}

.form-feedback__legend--contacts {
  display: none;
}

.form-feedback__input--contacts {
  margin: 0 auto;
}

.form-feedback__label--contacts {
  margin-left: 0;
}

.form-contact__fieldset--contacts {
  width: 100%;
}

@media (min-width: @tablet-width) {
  .form-contact__contacts-wrapper {
    margin: 0 auto;
    display: flex;
    width: 100%;
    order: 2;
    margin-bottom: 70px;
  }

  .form-feedback__input--contacts {
    width: 376px;
  }

  .form-feedback__name-item--contacts {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    /* justify-content: flex-start; */
    align-items: flex-start;
    width: 50%;
  }

  .form-feedback__label--contacts {
    padding-left: 0;
    margin-left: 0;
  }

  .form-feedback__legend--contacts {
    display: block;
  }

  .form-contact__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
    padding-top: 40px;
  }

  .form-contact__fieldset--contacts {
    border-width: 2px;
    border-style: groove;
    border-color: #ececec;
  }
}

@media (min-width: @desktop-width) {
  .form-contact__contacts-wrapper {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin: 0 auto;
  }

  .form-feedback__label--contacts {
    padding-left: 50px;
  }


  .form-contact__list {
    width: 100%;
    display: flex;
    flex-direction: row;
    list-style: none;
    padding-left: 0;
    padding-top: 40px;
    display: flex;
    justify-content: space-around;
  }

  .form-feedback__name-item--phone::before {
    content: "";
    position: absolute;
    width: 59px;
    height: 59px;
    transform: translate(360px, -37px);
    background-color: #ececee;
    background-image: url("../img/phone-svg.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  .form-feedback__name-item--email::before {
    content: "";
    position: absolute;
    width: 59px;
    height: 59px;
    transform: translate(360px, -37px);
    background-color: #ececee;
    background-image: url("../img/mail-svg.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  .form-contact__fieldset--contacts {
    margin-top: -58px;
    margin-bottom: 27px;
    margin-left: 0;
  }
}
