.form-feedback__list-message {
  width: 100%;
  text-align: center;
  list-style: none;
  background-color: #ececec;
  padding-top: 20px;
  padding-bottom: 10px;
  padding-left: 0;
  margin: 0 auto;
}

.form-message__achivments-item {
  margin-bottom: 63px;
}

.form-message__achivments-wrapper {
  background-color: #ececec;
  width: 320px;
  margin: 0 auto;
  padding-top: 10px;
}

.form-feedback__input {
  display: block;
  padding: 14px 20px 15px;
  box-sizing: border-box;
  min-height: 63px;
  border: 2px solid lightgray;
  border-radius: 2px;
  font: inherit;
  font-size: 18px;
  line-height: 30px;
  width: 100%;
}

.form-message__checkbox {
  display: none;
}

.form-message__label-checkbox {
  position: relative;
  display: block;
  padding-top: 5px;
  padding-bottom: 4px;
  font-size: 18px;
  text-align: left;
  line-height: 30px;
  cursor: pointer;
  width: 260px;
  margin-left: 40px;
  margin-bottom: -10px;
}


.form-message__label-checkbox::before {
  content: "";
  left: 0;
  top: 50%;
  transform: translate(-54px, 33px);
  width: 35px;
  height: 35px;
  border-radius: 2px;
  margin-left: 35px;
  position: relative;
  display: block;
  /* padding-left: 69px; */
  padding-top: -16px;
  /* padding-bottom: 4px; */
  /* font-size: 18px; */
  /* line-height: 30px; */
  cursor: pointer;
  border: 2px solid;
}

&:hover::before {
  border-color: black;
}

&:active::before {
  border-color: #d22856;
}

.form-message__label-checkbox::after {
  content: none;
  position: absolute;
  left: -47px;
  top: 47px;
  transform: rotate(-45deg);
  width: 21px;
  height: 10px;
  border-left: 2px solid #d22856;
  border-bottom: 2px solid #d22856;
  margin-left: 37px;
}

.form-message__title-checkbox {
  padding-left: 40px;
}

&::after:hover {
  border: 2px solid black;
}

&:checked + .form-message__label-checkbox {
  &::after {
    content: "";
  }
}

@media (min-width: @tablet-width) {
  .form-message__achivments-wrapper {
    width: 100%;
    background-color: white;
    order: 3;
  }

  .form-message__label-checkbox::before {
    transform: translate(-50px, 35px);
  }

  .form-message__label-checkbox::after {
    left: -43px;
    top: 51px;
  }

  .form-feedback__fieldset-messages {
    width: 100%;
    text-align: center;
    list-style: none;
    background-color: white;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 0;
    margin: 0 auto;
  }

  .form-message__label-checkbox {
    position: relative;
    display: block;
    padding-top: 5px;
    padding-bottom: 4px;
    font-size: 18px;
    line-height: 30px;
    cursor: pointer;
    width: 260px;
    margin-left: 40px;
    margin-bottom: -10px;
  }

  .form-feedback__list-message {
    width: 606px;
    text-align: center;
    list-style: none;
    background-color: white;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 0;
    /* margin: 0 auto; */
  }

  .form-message__fieldset--achivments {
    width: 100%;
    border-style: groove;
    border-color: #ececec;
    border-image: initial;
    margin-bottom: 69px;
    border-width: 2px;
    padding: 0;
  }

  .form-message__title-checkbox {
    padding-left: 50px;
  }
}

@media (min-width: @desktop-width) {
  .form-feedback__list-message {
    width: 100%;
    justify-content: center;
    text-align: center;
    list-style: none;
    background-color: white;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 0;
    /* margin: 0 auto; */
    display: flex;
    flex-wrap: wrap;
  }

  .form-feedback__input {
    width: 50%;
  }

  .form-message__label-checkbox {
    margin-right: -38px;
  }

  .form-message__title-checkbox {
    padding-left: 33px;
  }

  .form-message__achivments-wrapper {
    width: 100%;
  }
}
